.woocommerce .automatewoo-birthday-section.form-row {
  margin: 0 0 1.41575em; }

.woocommerce .automatewoo-birthday-section .automatewoo-birthday-field {
  margin: 2px 0 4px;
  max-width: 640px; }
  .woocommerce .automatewoo-birthday-section .automatewoo-birthday-field__select {
    float: left;
    width: auto;
    margin: 0 0 0 2%;
    min-height: 2.2em; }
    .woocommerce .automatewoo-birthday-section .automatewoo-birthday-field__select:first-child {
      margin: 0; }
    .woocommerce .automatewoo-birthday-section .automatewoo-birthday-field__select--year {
      width: 30%; }
    .woocommerce .automatewoo-birthday-section .automatewoo-birthday-field__select--day {
      width: 26%; }
    .woocommerce .automatewoo-birthday-section .automatewoo-birthday-field__select--month {
      width: 40%; }
    .woocommerce .automatewoo-birthday-section .automatewoo-birthday-field__select[disabled] {
      color: #999; }
  .woocommerce .automatewoo-birthday-section .automatewoo-birthday-field--no-year .automatewoo-birthday-field__select {
    width: 49%; }

.woocommerce .automatewoo-birthday-section__description, .woocommerce .automatewoo-birthday-section__already-set-text {
  font-style: italic;
  display: inline-block; }
